<template>
	<el-container>
		<el-header style="text-align: left; font-size: 12px">

			<el-button type="primary" plain @click="dialogVisible = true">添加</el-button>
			<el-button type="primary" plain @click="dialogVisibleEdit = true">编辑</el-button>
			<el-button type="danger" plain @click="delData">删除</el-button>

		</el-header>
		<hr />
		<el-main>
			<el-table :data="tableData" @selection-change="handleSelectionChange" ref="multipleTable" stripe>
				<el-table-column type="selection"></el-table-column>
				<el-table-column prop="id" label="编码" width="140">
				</el-table-column>
				<el-table-column prop="date" label="日期" width="140">
				</el-table-column>
				<el-table-column prop="name" label="姓名" width="120">
				</el-table-column>
				<el-table-column prop="address" label="地址">
				</el-table-column>
			</el-table>
			<el-pagination background layout="prev, pager, next" :page-size='10' :total="tableData.length">
			</el-pagination>
		</el-main>

		<el-dialog title="提示" :visible.sync="dialogVisible" width="30%" @close="resetForm">
			<el-form :model="form">
				<el-form-item label="编码">
					<el-input v-model="form.id"></el-input>
				</el-form-item>
				<el-form-item label="日期">
					<el-input v-model="form.date"></el-input>
				</el-form-item>
				<el-form-item label="姓名">
					<el-input v-model="form.name"></el-input>
				</el-form-item>
				<el-form-item label="地址">
					<el-input v-model="form.address"></el-input>
				</el-form-item>
			</el-form>
			<span slot="footer" class="dialog-footer">
				<el-button @click="dialogVisible = false">取消</el-button>
				<el-button type="primary" @click="submitForm">确定</el-button>
			</span>
		</el-dialog>

		<el-dialog title="提示" :visible.sync="dialogVisibleEdit" width="30%" @close="resetForm">
			<el-form :model="formEdit">
				<el-form-item label="编码">
					<el-input :value="formEdit.id" @input="updateName"></el-input>
				</el-form-item>
				<el-form-item label="日期">
					<el-input v-model="formEdit.date"></el-input>
				</el-form-item>
				<el-form-item label="姓名">
					<el-input v-model="formEdit.name"></el-input>
				</el-form-item>
				<el-form-item label="地址">
					<el-input v-model="formEdit.address"></el-input>
				</el-form-item>
			</el-form>
			<span slot="footer" class="dialog-footer">
				<el-button @click="dialogVisibleEdit = false">取消</el-button>
				<el-button type="primary" @click="editForm">确定</el-button>
			</span>
		</el-dialog>
	</el-container>
</template>

<script>
	export default {
		name: 'UserList',
		data() {

			return {
				tableData: [{
					id: '001',
					date: '2016-05-02',
					name: 'shuvi',
					address: 'human 1518'
				}, {
					id: '001',
					date: '2016-05-02',
					name: 'shuvi',
					address: 'human 1518'
				}, {
					id: '001',
					date: '2016-05-02',
					name: 'shuvi',
					address: 'human 1518'
				}, ],
				dialogVisible: false,
				dialogVisibleEdit: false,
				selectedRow: null,
				form: {
					id: '',
					date: '',
					name: '',
					address: '',
				},
				formEdit: {}

			}
		},
		methods: {
			/* addData() {
				let item = {
					date: '2016-05-02',
					name: 'shuvi',
					address: 'human 1518'
				};

				this.tableData.unshift(item)
			}, */
			editData() {

			},
			delData() {
				this.tableData.pop()
			},
			updateName(newValue) {
				this.formEdit.id = newValue
			},

			submitForm() {
				// 验证输入的邮箱和姓名，然后处理提交逻辑
				console.log('提交的信息:', JSON.stringify(this.form));
				let subData = {
					...this.form
				}

				this.tableData.unshift(subData)
				console.log('数组', JSON.stringify(this.tableData));


				// 在这里可以添加邮箱和姓名的验证逻辑
				this.dialogVisible = false; // 关闭对话框
				this.resetForm(); // 重置表单
			},
			editForm() {

			},
			resetForm() {
				this.form = {};
			},
			handleSelectionChange(selection) {
				if (selection.length > 0) {
					// 当有新行被选中时，取消之前选中的行，并保留最新的那一行
					const currentSelected = selection[selection.length - 1];
					if (this.selectedRow && this.selectedRow !== currentSelected) {
						this.$refs.multipleTable.toggleRowSelection(this.selectedRow, false);
					}
					this.selectedRow = currentSelected;

					this.formEdit = this.selectedRow
					console.log('当前' + JSON.stringify(this.formEdit));
				} else {
					// 如果没有行被选中，清空 selectedRow
					this.selectedRow = null;
				}
			},
			/* open() {
				this.$prompt('请输入邮箱', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					inputPattern: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
					inputErrorMessage: '邮箱格式不正确'
				}).then(({
					value
				}) => {
					this.$message({
						type: 'success',
						message: '你的邮箱是: ' + value,
					});
				});
			}, */
		}

	}
</script>

<style scoped>
	hr {
		color: black;
		width: 100vw;
	}

	.el-header {
		background-color: white;
		color: #333;
		line-height: 60px;
	}

	.el-aside {
		color: #333;
	}
</style>